<template>
  <div class="filter-content">
    <div class="filter-group">
      <el-divider></el-divider>

      <el-checkbox-group v-model="typeSelected">
        <div class="filter-item" v-for="item of types" :key="item">
          <el-checkbox :label="item" color="white"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>

    <div class="filter-group">
      <el-divider></el-divider>

      <el-checkbox-group v-model="levelSelected">
        <div class="filter-item" v-for="item of levels" :key="item">
          <el-checkbox :label="item"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      visible: false,
      types: ["", "", "", "", ""],
      levels: ["", "", ""],
      typeSelected: [],
      levelSelected: []
    };
  },
  mounted() {
    this.initType();
  },
  watch: {
    typeSelected() {
      this.filter();
    },
    levelSelected() {
      this.filter();
    }
  },
  methods: {
    initType() {
      this.typeSelected = this.types;
      this.levelSelected = this.levels;
    },
    filter() {
      this.project &&
        this.project.features.forEach(f => {
          f.active =
            this.typeSelected.includes(f.properties[""]) &&
            this.levelSelected.includes(f.properties[""]);
        });

      this.$app.$emit("refresh");
    }
  }
};
</script>
