<template>
  <div class="intro container" v-show="info.type || info.level || info.desc">
    <vue-scroll>
      <div class="intro-wrap" ref="wrap">
        <div class="intro-item" v-show="info.type && type.value !== 'hehu'">
          <span class="intro-title"></span>
          <span class="intro-value">{{info.type}}</span>
        </div>

        <div class="intro-item" v-show="info.level">
          <span class="intro-title"></span>
          <span class="intro-value">{{info.level}}</span>
        </div>

        <div class="intro-item" v-show="info.desc">
          <span class="intro-title"></span>
          <span class="intro-value">{{info.desc}}{{info.desc}}{{info.desc}}</span>
        </div>
      </div>
    </vue-scroll>
  </div>
</template>

<script>
export default {
  props: ["info"],
  computed: {
    type () {
      return this.$store.state.type
    }
  },
  watch: {
    info () {
      this.setHeight()
    }
  },
  methods: {
    setHeight () {
      setTimeout(() => {
        const wrapHeight = this.$refs['wrap'].clientHeight

        this.$el.style.height = wrapHeight + 30 + 'px'
      }, 100)
    }
  }
};
</script>

<style lang="scss" scoped>
.intro {
  width: 100%;
  padding: 10px;
  text-align: left;
  box-sizing: border-box;
  pointer-events: auto;
  max-height: 100%;
  overflow: auto;
  font-size: 1.1rem;
  overflow: hidden;

  .intro-item {
    margin-top: 2px;
  }

  .intro-title {
    margin-right: 6px;
    color: #e1c33e;
  }

  .intro-value {
    color: lightgray;
  }
}
</style>