<template>
  <div class="map-view">
    <v-header />

    <base-map />

    <search />

    <v-filter />

    <detail />

    <gallery />
  </div>
</template>

<script>
import VHeader from "@/components/common/Header";
import BaseMap from "@/components/common/BaseMap";
import VFilter from "@/components/common/Filter";
import Detail from "@/components/common/Detail";
import Gallery from "@/components/common/Gallery";
import Search from "@/components/common/Search";

import L from "leaflet";
require("leaflet.animatedmarker/src/AnimatedMarker");
require("leaflet.markercluster");

import wkt from "terraformer-wkt-parser";
import bbox from "@turf/bbox";
import mask from "@turf/mask";
import loader from "load-script-promise";

let layer = null;
let movingMarkers = [];

const bounds = []

export default {
  components: {
    BaseMap,
    VHeader,
    VFilter,
    Detail,
    Gallery,
    Search
  },
  computed: {
    mapLoaded() {
      return this.$store.state.mapLoaded;
    },
    type() {
      return this.$store.state.type;
    },
    project() {
      return this.$store.state.project;
    },
    feature() {
      return this.$store.state.feature;
    }
  },
  watch: {
    mapLoaded() {
      layer && layer.addTo(this.$map);

      this.loadBorder();
    },
    type() {
      this.$store.dispatch("fetch_project", this.type.value);

      this.setMapView();
    },
    project() {
      this.setData();
    },
    feature() {
      if (this.feature) {
        this.twinkle();
      }

      if (this.type.value === "lvdao") {
        this.movingMarker();
      }
    }
  },
  mounted() {
    layer = L.geoJSON(null, {
      filter: feature => feature.active,
      style: feature => {
        if (this.type.value === "lvdao") {
          return {
            stroke: true,
            color: "rgb(0,115,76)",
            weight: 5,
            opacity: 0.8
          };
        }

        if (this.type.value === "baohuqu") {
          return {
            stroke: true,
            color: "rgb(0,169,230)",
            opacity: 0.8
          };
        }
      },
      pointToLayer: (feature, latlng) => {
        return this.getMarker(latlng);
      },
      onEachFeature: (feature, layer) => {
        const prop = feature.properties;
        const name =
          (prop && prop[""]) || prop[""] || prop[""];

        layer
          .bindTooltip(name, {
            offset: [10, 0],
            direction: "right",
            sticky: true
          })
          .openTooltip();

        layer.on("click", e => {
          this.$store.commit("feature_changed", feature);
        });
      }
    });

    if (this.$map) {
      layer.addTo(this.$map);
    }

    this.$app.$on("refresh", () => this.setData());
  },
  methods: {
    getMarker(latlng) {
      const icon = L.icon({
        iconUrl: `./img/${this.type.value}.png`,
        iconSize: [24, 24],
        className: "c-marker"
      });

      return L.marker(latlng, { icon });
    },
    loadBorder() {
      const map = this.$map;

      loader.loadScript("./region.js").then(() => {
        const region = window.region;
        region.children.forEach(item => {
          const geometry = wkt.parse(item.GEOMETRY);

          L.geoJSON(geometry, {
            style: {
              color: "rgb(26, 47, 101, 0.5)",
              weight: 2,
              stroke: 1,
              fillOpacity: 0
            }
          }).addTo(map);
        });

        const geometry = wkt.parse(region.GEOMETRY);
        const masked = mask(geometry)

        L.geoJSON(masked, {
          style: {
            color: "rgb(26, 47, 101, 1)",
            weight: 5,
            stroke: 5,
            fillOpacity: 1
          }
        }).addTo(map);

        const { MAXX, MAXY, MINX, MINY } = region;

        bounds.push([MINY, MINX])
        bounds.push([MAXY, MAXX])

        this.setMapView()
      });
    },
    setData() {
      layer.clearLayers();
      this.project && layer.addData(this.project);
    },
    setMapView() {
      this.$map.fitBounds(bounds);
    },
    twinkle() {
      const map = this.$map;
      const b = bbox(this.feature);
      const bounds = [
        [b[1], b[0]],
        [b[3], b[2]]
      ];
      map.fitBounds(bounds, {
        maxZoom: 12
      });

      layer.eachLayer(f => {
        if (f.feature === this.feature) {
          const element = f.getElement();
          element.classList.add("animate");

          setTimeout(() => {
            element.classList.remove("animate");
          }, 5000);
        }
      });
    },
    movingMarker() {
      const map = this.$map;

      const icon = L.icon({
        iconUrl: "./img/moving.png",
        iconSize: [32, 32]
      });

      const geometry = this.feature.geometry;

      if (geometry.type === "MultiLineString") {
        geometry.coordinates.forEach(c => {
          const coordinates = c.map(cc => [cc[1], cc[0]]);
          const animatedMarker = L.animatedMarker(coordinates, {
            autoStart: false,
            icon,
            distance: 300,
            interval: 100,
            onEnd: () => {
              animatedMarker.remove();
            }
          });
          map.addLayer(animatedMarker);
          animatedMarker.start();

          movingMarkers.push(animatedMarker);
        });
      } else {
        const coordinates = geometry.coordinates.map(c => [c[1], c[0]]);
        const animatedMarker = L.animatedMarker(coordinates, {
          autoStart: false,
          icon,
          distance: 300, // meters
          interval: 100,
          onEnd: () => {
            animatedMarker.remove();
          }
        });
        map.addLayer(animatedMarker);
        animatedMarker.start();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.map-view {
  position: relative;
  width: 100%;
  height: 100%;

  ::v-deep .c-marker {
    background-color: rgba(137, 185, 92, 0.8);
    padding: 3px;
    border-radius: 50%;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
    box-sizing: border-box;

    &:hover {
      border: solid 1px white;
    }
  }

  ::v-deep .animate {
    animation: twinkle 1s 5;
  }

  ::v-deep .leaflet-container {
    background: #1a2f65;
  }

  ::v-deep .leaflet-tooltip {
    font-size: 1.1rem;
  }
}
</style>