import Vue from 'vue'
import Vuex from 'vuex'
import loader from 'load-script-promise'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    mapLoaded: false,
    type: null,
    project: null,
    feature: null,
    media: null
  },
  mutations: {
    map_loaded(state) {
      state.mapLoaded = true
    },
    type_changed(state, type) {
      if (state.type !== type) {
        state.project = null
        state.feature = null
      }
      state.type = type
    },
    project_changed(state, project) {
      state.project = project
    },
    feature_changed(state, feature) {
      if (state.feature === feature) {
        state.feature = null

        setTimeout(() => {
          state.feature = feature
        }, 200)
      } else {
        state.feature = feature
      }
    },
    media_changed(state, media) {
      state.media = media
    }
  },
  actions: {
    fetch_project({
      commit
    }, type) {
      loader.loadScript(`./data/${type}.js`).then(() => {
        const data = window.data
        data.features.map(feature => feature.active = true)

        commit('project_changed', data)
      })
    }
  }
})