<template>
  <div class="header container">
    <div class="title"></div>

    <div class="items">
      <div
        class="item"
        :class="{active: type === item}"
        v-for="item of items"
        :key="item.value"
        @click="itemChanged(item)"
      >
        <div class="item-icon">
          <icon :name="item.value" scale="3.2"></icon>
        </div>
        <div class="item-text">{{item.label}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    type() {
      return this.$store.state.type;
    }
  },
  data() {
    return {
      items: [
        {
          label: "",
          value: "yichan"
        },
        {
          label: "",
          value: "baohuqu"
        },
        {
          label: "",
          value: "chengzhen"
        },
        {
          label: "",
          value: "xiangcun"
        },
        {
          label: "",
          value: "hehu"
        },
        {
          label: "",
          value: "lvdao"
        }
      ]
    };
  },
  methods: {
    itemChanged(item) {
      this.$store.commit("type_changed", item);
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 80px;
  z-index: 1;
  padding: 0 20px;

  display: flex;
  align-items: center;
  box-sizing: border-box;

  .title {
    flex: 1;
    font-size: 2.4rem;
    font-weight: bold;
    color: white;
    text-align: left;
    letter-spacing: 5px;
  }

  .items {
    display: flex;
    height: 100%;
  }

  .item {
    cursor: pointer;
    padding: 12px;
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;

    &-icon {
      flex: 2;
    }

    &-text {
      flex: 1;
    }

    &.active,
    &:hover {
      color: rgba(26, 47, 101, 1);
      background-color: rgb(225, 195, 62);
    }
  }
}
</style>