<template>
  <div
    class="filter container"
    v-loading="!project"
    v-if="type && (type.value === 'baohuqu' || type.value === 'yichan' || type.value === 'hehu' || type.value === 'xiangcun')"
  >
    <filter-yichan v-if="project && type.value === 'yichan'" />
    <filter-baohuqu v-if="project && type.value === 'baohuqu'" />
    
    <filter-hehu v-if="project && (type.value === 'hehu' || type.value === 'xiangcun') " />
  </div>
</template>

<script>
import FilterYichan from "./FilterYichan";
import FilterBaohuqu from "./FilterBaohuqu";
import FilterHehu from "./FilterHehu";

export default {
  components: {
    FilterYichan,
    FilterBaohuqu,
    FilterHehu
  },
  computed: {
    project() {
      return this.$store.state.project;
    },
    type() {
      return this.$store.state.type;
    }
  },
  data() {
    return {
      visible: false
    };
  }
};
</script>

<style lang="scss" scoped>
.filter {
  position: absolute;
  width: 180px;
  min-height: 160px;
  top: 140px;
  right: 10px;
  z-index: 1;
  padding: 3px 6px;
  max-height: calc(100% - 200px);
  overflow: auto;

  ::v-deep .filter-item {
    text-align: left;
    padding: 3px;
  }

  ::v-deep .filter-title {
    font-size: 1.2rem;
    
  }

  ::v-deep .el-checkbox {
    color: white;

    &.is-checked {
      .el-checkbox__label {
        color: rgb(225, 195, 62);
      }

      .el-checkbox__inner {
        background-color: rgb(225, 195, 62);
        border-color: rgb(225, 195, 62);
      }
    }
  }

  ::v-deep .el-tree {
    background: unset;
    color: white;

    .el-tree-node__content {
      background: unset ;
    }
  }
}
</style>