<template>
  <div class="img-box container" v-show="images && images.length">
    <el-carousel height="200px">
      <el-carousel-item v-for="item of images" :key="item">
        <img class="img" :src="`http://demo.zjditu.cn/demo/mlhz/data/thumbnail/${item}`" fit="cover" @click="open(item)"/>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  props: ["images"],
  methods: {
    open (name) {
      const media = {
        type: 'image',
        url: `http://demo.zjditu.cn/demo/mlhz/data/img/${name}`
      }

      this.$store.commit('media_changed', media)
    }
  }
};
</script>

<style lang="scss" scoped>
.img-box {
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  pointer-events: auto;

  .img {
    border: solid 2px whitesmoke;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
  }
}
</style>