<template>
  <div class="base-map">
    <div id="base-map"></div>
    <div class="map-tool-container">
      <map-tool />
    </div>
  </div>
</template>

<script>
import L from "leaflet";
import Vue from "vue";
import MapTool from "@/components/common/MapTool";
import "leaflet/dist/leaflet.css";

export default {
  components: {
    MapTool
  },
  mounted() {
    const map = L.map("base-map", {
      center: [30, 120],
      zoom: 13,
      crs: L.CRS.EPSG4326,
      attributionControl: false,
      zoomControl: false,
      closePopupOnClick: false
    });

    Vue.prototype.$map = map;

    this.$store.commit("map_loaded");

    this.$app.$on("add-layer", opt => {
      this.addLayer(opt);
    });

    this.$app.$on("remove-layer", id => {
      this.removeLayer(id);
    });
  },
  methods: {
    addLayer({ id = "", type, url, config = {} }) {
      let layer = null;
      if (type === "xyz") {
        layer = L.tileLayer(url, config);
      }

      layer.id = id;
      layer.addTo(this.$map);
    },
    removeLayer(id) {
      let layer = null;
      this.$map.eachLayer(lyr => {
        if (lyr.id && lyr.id === id) {
          layer = lyr;
        }
      });

      if (layer) {
        layer.remove()
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.base-map {
  position: relative;
  width: 100%;
  height: 100%;
  background: gray;
  z-index: 0;

  #base-map {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .map-tool-container {
    position: absolute;
    right: 10px;
    bottom: 10px;
    z-index: 1000;
  }
}
</style>