<template>
  <div class="title container">
    <div class="title-text">{{name}}</div>

    <div class="title-close" @click="close">
      <icon name="close" scale="1.2"></icon>
    </div>
  </div>
</template>

<script>
export default {
  props: ["name"],
  methods: {
    close() {
      this.$store.commit("feature_changed");
    }
  }
};
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  pointer-events: auto;
  padding: 10px;
  font-size: 1.2rem;

  &-text {
    flex: 1;
    text-align: left;
  }

  &-close {
    cursor: pointer;
  }
}
</style>