<template>
  <div class="detail" v-show="feature">
    <div class="title-container">
      <v-title :name="name" />
    </div>

    <div class="img-box-container">
      <img-box :images="images" />
    </div>

    <div class="intro-container">
      <intro :info="info" />
    </div>
  </div>
</template>

<script>
import ImgBox from "./ImgBox";
import Intro from "./Intro";
import VTitle from "./Title"

export default {
  components: {
    ImgBox,
    Intro,
    VTitle
  },
  computed: {
    feature() {
      return this.$store.state.feature;
    }
  },
  data() {
    return {
      images: [],
      info: {},
      name: ''
    };
  },
  watch: {
    feature() {
      if (this.feature) {
        const properties = this.feature.properties

        const zp = properties[""];
        if (zp) {
          this.images = zp.split(",");
        } else {
          this.images = [];
        }

        this.info = {
          desc: properties[""],
          level: properties[""],
          type: properties[""]
        };

        this.name = properties[""] || properties[""] || properties[""];
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.detail {
  position: absolute;
  left: 10px;
  top: 90px;
  z-index: 1111;
  width: 300px;
  height: calc(100% - 110px);
  display: flex;
  flex-direction: column;
  pointer-events: none;

  .img-box-container {
    padding-top: 10px;
  }

  .intro-container {
    flex: 1;
    padding-top: 10px;
    overflow: hidden;
  }
}
</style>