<template>
  <div class="gallery" v-show="media">
    <div class="controller">
      <i class="el-icon-close" title="" @click="close"></i>
    </div>
    <div class="content">
      <div class="img-box">
        <div class="image-map" id="image" v-loading="loading"></div>
      </div>
    </div>
  </div>
</template>

<script>
import L from "leaflet";

export default {
  name: "gallery",
  computed: {
    media() {
      return this.$store.state.media;
    }
  },
  data() {
    return {
      loading: false
    };
  },
  watch: {
    media() {
      if (this.media && this.media.type === "image") {
        this.$nextTick(() => {
          this.setImage();
        });
      }
    }
  },
  methods: {
    close() {
      this.$store.commit("media_changed");
    },
    setImage() {
      this.loading = true;
      this.layer && this.layer.remove();

      if (!this.map) {
        this.map = L.map("image", {
          minZoom: -3,
          crs: L.CRS.Simple,
          attributionControl: false
        });
      }

      const image = new Image();
      image.src = this.media.url;

      image.onload = () => {
        this.loading = false;
        const bounds = [
          [0, 0],
          [image.height, image.width]
        ];

        this.map.fitBounds(bounds);

        this.layer = L.imageOverlay(this.media.url, bounds).addTo(this.map);
      };
    }
  }
};
</script>

<style lang="scss">
.gallery {
  position: fixed;
  top: 60px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 111111;
  color: #fff;

  .controller {
    position: absolute;
    right: 20px;
    top: 20px;
    font-size: 1.6em;
    cursor: pointer;
    z-index: 1;
  }

  .content {
    position: relative;
    color: #fff;
    height: 100%;

    .img-box {
      position: relative;
      padding: 60px 10px;
      height: 100%;

      .title {
        position: absolute;
        top: 30px;
        text-align: center;
      }

      .image-map {
        position: relative;
        width: 100%;
        height: calc(100% - 120px);
        border: solid 2px #fff;
        overflow: hidden;
      }
    }

    .iframe-box {
      width: 100%;
      height: 100%;

      iframe {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>

