<template>
  <div class="search container" v-if="project">
    <el-autocomplete
      class="inline-input"
      v-model="item"
      :fetch-suggestions="querySearch"
      placeholder=""
      @select="handleSelect"
      size="small"
      suffix-icon="el-icon-search"
    ></el-autocomplete>
  </div>
</template>

<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      item: null,
      items: []
    };
  },
  mounted() {
    this.setData();
  },
  watch: {
    project() {
      this.setData();
    }
  },
  methods: {
    querySearch(keyword, cb) {
      let results = this.items;
      if (keyword) {
        results = results.filter(f => {
          return f.value.indexOf(keyword) >= 0;
        });
      }

      cb(results);
    },
    setData() {
      if (!this.project) {
        return;
      }

      this.items = this.project.features.map(f => {
        const value =
          f.properties[""] ||
          f.properties[""] ||
          f.properties[""];
        return {
          value
        };
      });
    },
    handleSelect(item) {
      const name = item.value;

      this.project.features.forEach(f => {
        const value =
          f.properties[""] ||
          f.properties[""] ||
          f.properties[""];

        if (value === name) {
          this.$store.commit('feature_changed', f)
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.search {
  position: absolute;
  width: 180px;
  top: 90px;
  right: 10px;
  z-index: 1;
  padding: 3px 6px;

  &-input {
    width: 100%;
    box-sizing: border-box;
  }

  ::v-deep .el-input__inner {
    background-color: unset;
    color: white;
    border: none;
  }
}
</style>