<template>
  <div class="filter-content">
    <div class="filter-group">
      <el-divider></el-divider>

      <el-checkbox-group v-model="typeSelected">
        <div class="filter-item" v-for="item of types" :key="item">
          <el-checkbox :label="item" color="white"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>

    <div class="filter-group">
      <el-divider></el-divider>

      <el-checkbox-group v-model="itemSelected">
        <div class="filter-item" v-for="item of items" :key="item">
          <el-checkbox :label="item" color="white"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      visible: false,
      types: ["", ""],
      items: [],
      typeSelected: [],
      itemSelected: []
    };
  },
  mounted() {
    this.initType();
  },
  watch: {
    typeSelected() {
      this.filter();
    },
    itemSelected() {
      this.filter();
    }
  },
  methods: {
    initType() {
      const items = [];
      this.project.features.forEach(f => {
        items.push(f.properties[""]);
      });

      this.items = [...new Set(items)];

      this.typeSelected = this.types;
      this.itemSelected = this.items;
    },
    filter() {
      this.project &&
        this.project.features.forEach(f => {
          f.active =
            this.typeSelected.includes(f.properties[""]) &&
            this.itemSelected.includes(f.properties[""]);
        });

      this.$app.$emit("refresh");
    }
  }
};
</script>