<template>
  <div class="filter-content">
    <div class="filter-title"> {{project.features.length}} </div>

    <div class="filter-tree">
      <el-tree :data="items" @node-click="nodeSelected"></el-tree>
    </div>
  </div>
</template>


<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      visible: false,
      items: [
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] },
        { label: "", children: [] }
      ]
    };
  },
  mounted() {
    this.initData();
  },
  watch: {
    typeSelected() {
      this.filter();
    },
    levelSelected() {
      this.filter();
    }
  },
  methods: {
    initData() {
      const features = this.project.features;
      this.items.forEach(item => {
        const name = item.label;

        features.forEach(feature => {
          if (feature.properties[""].indexOf(name) >= 0) {
            feature.label =
              feature.properties[""] ||
              feature.properties[""] ||
              feature.properties[""];
            item.children.push(feature);
          }
        });
      });
    },
    nodeSelected(node) {
      if (!node.children) {
        this.$store.commit("feature_changed", node);
      }
    }
  }
};
</script>
