<template>
  <div class="map-tool container">
    <div class="map-tool-item" @click="changeMapType">
      <span class="map-tool-icon">
        <icon name="map" scale="2"></icon>
      </span>
      <span class="map-tool-text">{{activeMapType && activeMapType.label}}</span>
    </div>

    <div class="map-tool-item" @click="measure">
      <span class="map-tool-icon">
        <icon name="measure" scale="2"></icon>
      </span>
      <span class="map-tool-text"></span>
    </div>
  </div>
</template>

<script>
import L from "leaflet";
import leafletDraw from "leaflet-draw";
import length from "@turf/length";

export default {
  data() {
    return {
      mapTypes: [
        {
          label: "",
          values: [
            {
              id: "img",
              type: "xyz",
              url:
                "http://srv{s}.zjditu.cn/ZJDOM_2D/wmts?layer=imgmap&style=default&tilematrixset=default028mm&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpgpng&TileMatrix={z}&TileCol={x}&TileRow={y}",
              config: {
                subdomains: "123",
                zoomOffset: 1,
                bounds: [[29.188, 118.339], [30.565, 120.725]]
              }
            },
            {
              id: "img-anno",
              type: "xyz",
              url:
                "http://srv{s}.zjditu.cn/ZJDOMANNO_2D/wmts?layer=TDT_ZJIMGANNO&style=default&tilematrixset=default028mm&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpgpng&TileMatrix={z}&TileCol={x}&TileRow={y}",
              config: {
                subdomains: "123",
                zoomOffset: 1,
                minZoom: 9,
                bounds: [[29.188, 118.339], [30.565, 120.725]]
              }
            }
          ]
        },
        {
          label: "",
          values: [
            {
              id: "emap",
              type: "xyz",
              url:
                "http://srv{s}.zjditu.cn/ZJEMAP_2D/wmts?layer=TDT_ZJEMAP&style=default&tilematrixset=default028mm&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpgpng&TileMatrix={z}&TileCol={x}&TileRow={y}",
              config: {
                subdomains: "123",
                zoomOffset: 1,
                bounds: [[29.188, 118.339], [30.565, 120.725]]
              }
            },
            {
              id: "emap-anno",
              type: "xyz",
              url:
                "http://srv{s}.zjditu.cn/ZJEMAPANNO_2D/wmts?layer=TDT_ZJEMAPANNO&style=default&tilematrixset=default028mm&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpgpng&TileMatrix={z}&TileCol={x}&TileRow={y}",
              config: {
                subdomains: "123",
                zoomOffset: 1,
                minZoom: 9,
                bounds: [[29.188, 118.339], [30.565, 120.725]]
              }
            }
          ]
        }
      ],
      activeMapType: null
    };
  },
  watch: {
    activeMapType(n, o) {
      o &&
        o.values.forEach(l => {
          this.$app.$emit("remove-layer", l.id);
        });

      n.values.forEach(l => {
        this.$app.$emit("add-layer", l);
      });
    }
  },
  mounted() {
    this.activeMapType = this.mapTypes[1];
  },
  methods: {
    changeMapType() {
      this.activeMapType =
        this.activeMapType === this.mapTypes[0]
          ? this.mapTypes[1]
          : this.mapTypes[0];
    },
    measure() {
      const map = this.$map;

      if (this.drawing) {
        return
      }

      const layer = L.polyline([], {
        interactive: false
      }).addTo(map);

      this.layer = layer;

      const tempLayer = L.polyline([], {
        interactive: false
      }).addTo(map);
      let tempPoints = [];

      const end = () => {
        layer.remove();
        popup.remove();
        dblclickHandler();
      };

      const popup = L.popup({
        autoClose: false,
        closeButton: false
      });

      const setTipText = content => {
        const el = document.createElement("div");
        el.className = "measure-marker";

        const text = document.createElement("span");
        text.className = "measure-text";
        text.innerHTML = content;

        const close = document.createElement("span");
        close.className = "measure-close";

        close.addEventListener("click", () => {
          end();
        });

        el.appendChild(text);
        el.appendChild(close);

        return el;
      };

      const clickHandler = e => {
        layer.addLatLng(e.latlng);
        tempPoints[0] = e.latlng;
        this.drawing = true

        const len = length(layer.toGeoJSON(), { units: "kilometers" });

        popup
          .setLatLng(e.latlng)
          .setContent(setTipText(len.toFixed(2) + " "))
          .openOn(map);
      };

      const mousemoveHandler = e => {
        if (tempPoints.length) {
          tempPoints[1] = e.latlng;
          tempLayer.setLatLngs(tempPoints);
        }
      };

      const dblclickHandler = e => {
        tempPoints = null;
        tempLayer.remove();
        this.drawing = false

        map.off("click", clickHandler);
        map.off("mousemove", mousemoveHandler);
        map.off("dblclick", dblclickHandler);
      };

      map.on("click", clickHandler);
      map.on("mousemove", mousemoveHandler);
      map.on("dblclick", dblclickHandler);
    }
  }
};
</script>

<style lang="scss">
.map-tool {
  position: relative;
  display: flex;
  align-items: center;

  &-item {
    display: flex;
    align-items: flex-start;
    cursor: pointer;
    padding: 6px 8px;
    font-size: 1.1rem;

    &:hover {
      opacity: 0.8;
    }
  }

  &-icon {
    margin-right: 3px;
  }
}

div.leaflet-popup-content {
  margin: 5px;
}

div.leaflet-popup-content-wrapper {
  border-radius: 6px;
}

.measure-marker {
  display: flex;
  align-items: center;

  .measure-text {
    font-size: 16px;
  }

  .measure-close {
    height: 18px;
    width: 18px;
    display: block;
    background-image: url(~@/assets/img/close.png);
    background-size: contain;
    cursor: pointer;
  }
}
</style>